import {
  Component, Inject, OnInit, PLATFORM_ID, ViewChild,
  ViewContainerRef
} from '@angular/core';
import {
  User,
  ResponseListSectionDto,
  SectionService,
  HighlightProductVariantWarehouses,
  CompareTransactionTypes,
  AccountTypes,
  ScmAjakCuanService,
  AgentStatus,
  BalanceService,
  CategorySection,
  OfficialPartners,
  Warehouse,
  Highlight
} from '@youtap-web-lib';
import {AuthData} from '../../shared/data/auth.data';
import {MetaService} from '../../shared/service/meta.service';
import {Router} from "@angular/router";
import {GaService} from "../../shared/service/ga.service";
import {isPlatformBrowser, isPlatformServer} from '@angular/common';
import {environment} from "../../../environments/environment";
import {RemoteModuleLoaderService} from "../../shared/service/remote-module-loader.service";
import {TransferStateService} from '../../shared/service/transfer-state.service';

const STATE_KEY_SECTION = 'dataSection';

@Component({
  selector: 'youtap-bos-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {
  @ViewChild('overviewNoLogin', {read: ViewContainerRef}) overviewNoLogin!: ViewContainerRef;

  @ViewChild('overviewVerified', {read: ViewContainerRef}) overviewVerified!: ViewContainerRef;

  token = '';
  loadingListSections = false;
  loadingOverview = false;
  loadingAgent = false;
  loadingBalance = false;
  profile!: User;
  listSections!: ResponseListSectionDto;
  warehouseSectionId: string[] = [];
  supplierPage = 1;
  haveSupplier = true;
  overviewMerchantId = '';
  overviewType = CompareTransactionTypes.DAILY;
  haveLocation = true;
  isHidden = true;
  accountTypes = AccountTypes;
  agentStatus!: AgentStatus;
  balance!: number;
  isReady = false;
  beforeShowOverview = true;
  section = {
    categories: [] as CategorySection[],
    officialPartners: [] as OfficialPartners[],
    warehouse: {} as Warehouse,
    highlight: {} as Highlight
  }

  constructor(
    @Inject(PLATFORM_ID) private platformId: object,
    private sectionService: SectionService,
    public authData: AuthData,
    private metaService: MetaService,
    private scmAjakCuanService: ScmAjakCuanService,
    private balanceService: BalanceService,
    private router: Router,
    private gaService: GaService,
    private remoteModuleLoaderService: RemoteModuleLoaderService,
    private transferStateService: TransferStateService
  ) {
  }

  ngOnInit(): void {
    if (isPlatformBrowser(this.platformId)) {
      setTimeout(() => {
        this.isReady = true;
      }, 1000)
    }
    this.token = this.authData.token;
    this.profile = this.authData.user;
    this.getListSection();
    this.loadMicroFrontend()
    this.metaService.setMeta({
      title:
        'B2B Marketplace, Belanja Grosir Online Kebutuhan Usaha Terlengkap | Youtap BOS',
      description:
        'Youtap BOS, marketplace b2b Indonesia solusi belanja grosir online kebutuhan usaha dan monitor transaksi penjualan. Beli produk terlengkap dari supplier terpercaya dengan harga kompetitif',
      image:
        'https://storage.googleapis.com/production-youtap-public/banner/IlbfMVveQ=s0.png'
    });
    if (this.authData.token) {
      this.detailAgent();
    }
  }

  loadMicroFrontend() {
    const posRemoteUrl = environment.POS_REPORT_REMOTE_URL + '?time=' + Date.now();
    this.loadingOverview = true
    const exposedModule = this.token ? './OverviewVerified' : './OverviewNoLogin';
    this.remoteModuleLoaderService.loadModule(posRemoteUrl, exposedModule)
      .then((m) => {
        this.loadingOverview = false
        if (this.token) {
          this.overviewVerified.createComponent(m.OverviewVerifiedComponent)
        } else {
          this.overviewNoLogin.createComponent(m.OverviewNoLoginComponent)
        }
        setTimeout(() => {
          this.beforeShowOverview = false
        }, 0)
      })
      .catch(() => {
        this.loadingOverview = false;
        setTimeout(() => {
          this.beforeShowOverview = false
        }, 0)
      });

  }

  fetchDataListSection(isServer = false) {
    this.loadingListSections = true;
    this.sectionService.listSection().subscribe({
      next: ({data}) => {
        if (isServer) {
          this.transferStateService.set(STATE_KEY_SECTION, data);
        }
        this.loadingListSections = false;
        this.listSections = data;
        this.section.categories = data.listSection.sections.filter((val) => val.type === 'CATEGORY')[0].categories;
        this.section.officialPartners = data.listSection.sections.filter((val) => val.type === 'OFFICIAL_PARTNER')[0].officialPartners;
        this.section.warehouse = data.listSection.sections.filter((val) => val.type === 'WAREHOUSE')[0].warehouse;
        this.section.highlight = data.listSection.sections.filter((val) => val.type === 'HIGHLIGHT')[0].highlight;

        const warehouse = this.listSections.listSection.sections.filter(
          (val) => val.type === 'WAREHOUSE' && val.warehouse !== null
        );
        if (warehouse.length) {
          warehouse.map((sp) => {
            if (sp.warehouse) {
              this.warehouseSectionId.push(sp.warehouse.id);
            }
          });
        }
      },
      error: () => {
        this.loadingListSections = false;
      }
    });
  }

  getListSection() {
    if (isPlatformServer(this.platformId)) {
      this.fetchDataListSection(true);
    }
    if (isPlatformBrowser(this.platformId)) {
      const listSection = this.transferStateService.get<ResponseListSectionDto>(STATE_KEY_SECTION, {} as ResponseListSectionDto);
      if (listSection && listSection.listSection) {
        this.listSections = listSection;
        this.section.categories = this.listSections.listSection.sections.filter((val) => val.type === 'CATEGORY')[0].categories;
        this.section.officialPartners = this.listSections.listSection.sections.filter((val) => val.type === 'OFFICIAL_PARTNER')[0].officialPartners;
        this.section.warehouse = this.listSections.listSection.sections.filter((val) => val.type === 'WAREHOUSE')[0].warehouse;
        this.section.highlight = this.listSections.listSection.sections.filter((val) => val.type === 'HIGHLIGHT')[0].highlight;

        const warehouse = this.listSections.listSection.sections.filter(
          (val) => val.type === 'WAREHOUSE' && val.warehouse !== null
        );
        if (warehouse.length) {
          warehouse.map((sp) => {
            if (sp.warehouse) {
              this.warehouseSectionId.push(sp.warehouse.id);
            }
          });
        }
      } else {
        this.fetchDataListSection();
      }
    }
  }

  checkPromo(data: HighlightProductVariantWarehouses[]): boolean {
    const havedata = data.filter((val) => val.productVariantWarehouse !== null);

    return havedata.length !== 0 ? true : false;
  }

  onScroll() {
    this.supplierPage = this.supplierPage + 1;
  }

  toggleShowFullText() {
    this.isHidden = !this.isHidden;
  }

  detailAgent() {
    this.loadingAgent = true;
    this.scmAjakCuanService.detailAgent().subscribe({
      next: (data) => {
        this.loadingAgent = false;
        this.agentStatus = data.data.detailAgent.status;
        if (data) {
          this.getBalance();
        }
      },
      error: () => {
        this.loadingAgent = false;
      }
    });
  }

  getBalance() {
    this.loadingBalance = true;
    this.balanceService.getAffiliateBalance().subscribe({
      next: (data) => {
        this.loadingBalance = false;
        if (data) {
          this.balance = data.data.getAffiliateBalance.balance;
        }
      },
      error: () => {
        this.loadingBalance = false;
      }
    });
  }

  goToDetailAjakCuan() {
    this.gaService.addEvent('Ajakcuan_in_homepage', 'ajakcuan', '');
    this.router.navigate(['/ajakcuan/dashboard']);
  }

  setLocation() {
    if (this.token) {
      this.router.navigate(["/address/list"]);
    } else {
      this.router.navigate(["/location/set"], {});
    }
  }
}
