import { Component, Input, OnInit } from '@angular/core';
import { animate, style, transition, trigger } from '@angular/animations';
import { Router } from '@angular/router';
import {
  ListPurchasePackageDto,
  ListVoucher,
  PlanOptionTypes,
  SubscriptionPurchase,
  SubscriptionService,
  VoucherService,
  MembershipService,
  CurrentTier
} from '@youtap-web-lib';
import { GaService } from '../../../shared/service/ga.service';

@Component({
  selector: 'voucher-section',
  animations: [
    trigger('enterAnimation', [
      transition(':enter', [
        style({ height: '0', opacity: 0 }),
        animate('300ms', style({ height: '300px', opacity: 1 }))
      ]),
      transition(':leave', [
        style({ height: '400px', opacity: 1 }),
        animate('300ms', style({ height: '0', opacity: 0 }))
      ])
    ])
  ],
  templateUrl: './voucher-section.component.html'
})
export class VoucherSectionComponent implements OnInit {
  loading = false;
  listVoucher!: ListVoucher;
  listPurchasedPackage!: SubscriptionPurchase[];
  subscriptionPurchase!: SubscriptionPurchase;
  loadingPurchasedPackage = false;
  @Input() isNotLoggedIn = true;
  @Input() token = '';
  currentTier!: CurrentTier;
  tierName = 'basic';
  tierDisplayName = 'Basic';
  // icon = 'https://iili.io/JaroxI9.png';

  constructor(
    private router: Router,
    private voucherService: VoucherService,
    private membershipService: MembershipService,
    private subscriptionService: SubscriptionService,
    private gaService: GaService
  ) {}

  ngOnInit(): void {
    if (this.token) {
      this.getListVoucher();
      this.getPackagePurchasedList();
      this.getCurrentTier();
    }
  }

  getListVoucher() {
    this.voucherService.listVoucher().subscribe({
      next: ({ data }) => {
        this.loading = false;
        this.listVoucher = data.listVoucher;
      },
      error: (err) => {
        this.loading = false;
        return err;
      }
    });
  }
  getCurrentTier() {
    this.membershipService.currentTier().subscribe({
      next: ({ data }) => {
        this.loading = false;
        this.currentTier = data.currentTier;
        // if (data?.currentTier?.tier) {
        //   this.tierName = data.currentTier.tier.name;
        //   this.tierDisplayName = data.currentTier.tier.displayName;
        // }
      },
      error: (err) => {
        this.loading = false;
        return err;
      }
    });
  }

  getPackagePurchasedList() {
    this.loadingPurchasedPackage = true;
    const data: ListPurchasePackageDto = {
      listPurchasePackageInputPaid: {
        planOptionType: PlanOptionTypes.PAID,
        isExpired: false
      },
      listPurchasePackageInputPaidExpired: {
        planOptionType: PlanOptionTypes.PAID,
        isExpired: true
      }
    };

    this.subscriptionService.listPurchasePackage(data).subscribe({
      next: ({ data }) => {
        if (data) {
          this.loadingPurchasedPackage = false;
          this.listPurchasedPackage = data.PAID.subscriptionPurchases;

          // Sort the subscriptionPurchases array in ascending order based on displayName
          if (this.listPurchasedPackage) {
            this.listPurchasedPackage?.sort((a, b) => {
              const displayNameA = a.plan.displayName.toLowerCase();
              const displayNameB = b.plan.displayName.toLowerCase();

              if (displayNameA < displayNameB) {
                return -1;
              }
              if (displayNameA > displayNameB) {
                return 1;
              }
              return 0;
            });
            this.subscriptionPurchase = this.listPurchasedPackage[0];
          }
        }
      },
      error: () => {
        this.loadingPurchasedPackage = false;
      }
    });
  }

  goToProfileVoucher() {
    this.gaService.addEvent('Open Voucher Home', 'Voucher', '');
    this.router.navigate(['/profile/my-voucher']);
  }
  goToProfileDashboard() {
    this.gaService.addEvent('Open Voucher Home', 'Voucher', '');
    this.router.navigate(['/profile/dashboard']);
  }
  goToSubscription() {
    this.gaService.addEvent('Open Subscription', 'Subscription', '');
    this.router.navigate(['/subscription/list-subscription']);
  }
  goToMembershipDetail() {
    this.gaService.addEvent('membership_detail_section_home', 'profile', '');
    this.router.navigate(['/membership']);
  }
}
