import {
  Component,
  EventEmitter,
  Input,
  OnInit,
  Output,
  ViewEncapsulation
} from '@angular/core';
import { Router } from '@angular/router';
import { GaService } from '../../../service/ga.service';
import { Highlight } from '@youtap-web-lib';

@Component({
  selector: 'promo-section',
  templateUrl: './promo-section.component.html',
  styleUrls: ['./promo-section.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class PromoSectionComponent implements OnInit {
  @Input() promoImage =
    'https://ds393qgzrxwzn.cloudfront.net/resize/m640x640/cat1/img/images/0/xNsp9DPEtQ.jpg';
  @Output() clickAllPromo: EventEmitter<void> = new EventEmitter();
  @Input() highlight!: Highlight;
  htmlCode =
    '<img class="highlightImage" src="https://storage.googleapis.com/production-youtap-public/banner/Z7HSvestf=s0.png" alt="highlight-image">';

  constructor(private router: Router, private gaService: GaService) { }

  ngOnInit() {
    if (this.highlight) {
      this.htmlCode = this.highlight.description;
      this.handleFilterNull();
    }
  }

  handleFilterNull() {
    this.highlight.highlightProductVariantWarehouses =
      this.highlight.highlightProductVariantWarehouses
        .filter((item) => item?.productVariantWarehouse !== null)
        .filter(
          (item) => item?.productVariantWarehouse.productVariant !== null
        );
  }

  handleClickAllPromo() {
    this.router.navigate(['/promo/list-promo']);
  }

  goToDetailProduct(slug: string) {
    this.gaService.addEvent('promo Detail ' + slug, 'promo', '');
  }
}
