import { Component, ViewEncapsulation, Input, OnInit } from '@angular/core';
import SwiperCore, { Autoplay, Navigation, Pagination } from 'swiper';
import { Router } from '@angular/router';

import {
  ListBannerConfigurationInputDto,
  ListBannersResponseDto
} from '@youtap-web-lib';

import { GaService } from '../../../service/ga.service';
import { WindowActionService } from '../../../service/window-action.service';
import { BannerListSignalService } from '../../../service/signal/banner-list.signal.service';

SwiperCore.use([Autoplay, Navigation, Pagination]);

@Component({
  selector: 'slider-banner',
  template: `
    @defer (when !bannerListSignalService.loadingSlider$() && !isLoading) {
    <swiper
      [slidesPerView]="'auto'"
      [uniqueNavElements]="false"
      [centeredSlides]="true"
      [loop]="true"
      [preventInteractionOnTransition]="false"
      [spaceBetween]="8"
      [height]="200"
      [pagination]="{ clickable: true }"
      [autoplay]="{
        delay: bannerSliderDelay(),
        disableOnInteraction: false
      }">
      <ng-container
        *ngFor="
          let banner of bannerListSignalService.dataSlider$()
            .listBannerConfiguration?.bannerConfigurations
        ">
        <ng-container *ngFor="let items of banner.banners">
          @defer(when banner.status === 'ACTIVE') {
          <ng-template swiperSlide>
            <a (click)="openBanner(items)">
              <img
                class="image"
                [ngSrc]="items.image"
                alt="{{ banner.id }}"
                width="372"
                height="119"
                style="width:100%;" />
            </a>
          </ng-template>
          } @placeholder {
          <p-skeleton width="100%" height="15vh"></p-skeleton>
          }
        </ng-container>
      </ng-container>
    </swiper>
    } @placeholder {
    <div class="flex flex-col mx-[10px] rounded-lg py-[10px] gap-[15px]">
      <p-skeleton width="100%" height="15vh"></p-skeleton>
    </div>
    }
  `,
  styleUrl: './slider-banner.component.scss',
  encapsulation: ViewEncapsulation.None
})
export class SliderBannerComponent implements OnInit {
  @Input() LocationBanner = '';
  @Input() TypeBanner = '';
  @Input() PlatformBanner = '';
  isLoading = true;

  dataInputVariable!: ListBannerConfigurationInputDto;

  constructor(
    private router: Router,
    private gaService: GaService,
    private windowActionService: WindowActionService,
    public bannerListSignalService: BannerListSignalService
  ) {}

  ngOnInit(): void {
    setTimeout(() => {
      this.isLoading = false;
    }, 2000);
    this.getSliderBannerData();
  }

  getSliderBannerData() {
    this.dataInputVariable = {
      listBannerConfigurationInput: {
        location: this.LocationBanner,
        type: this.TypeBanner,
        platform: this.PlatformBanner
      }
    };

    this.bannerListSignalService.get(this.dataInputVariable);
  }

  openBanner(banner: ListBannersResponseDto) {
    if (this.LocationBanner === 'HOME') {
      this.gaService.addEvent(
        'home_banner_slider_click_' + banner.name,
        'home_banner_slider',
        ''
      );
    }
    if (this.LocationBanner === 'AJAK_CUAN') {
      this.gaService.addEvent(
        'ajakcuan_banner_slider_click_' + banner.name,
        'ajakcuan_banner_slider',
        '',
        true
      );
    }
    const platform = localStorage.getItem('platform') as string;
    if (platform !== null && platform !== 'web') {
      this.windowActionService.dispatchEvent('banner', {
        banner
      });
    } else {
      if (banner.type === 'EXTERNAL') {
        this.windowActionService.href(banner.linkWeb);
      } else {
        this.windowActionService.href(banner.url);
      }
    }
  }

  bannerSliderDelay(): number {
    const time = this.bannerListSignalService.bannerSliderDisplayCycle();
    const convertToSecond = this.convertMillisecondsToSeconds(time);
    return convertToSecond;
  }

  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  convertMillisecondsToSeconds(ms: any): number {
    return ms * 1000;
  }
}
